
.header{
    max-width: 100%;
    margin: 0 auto;
    display:inline-block;
    position: relative;
    
}


.header li{
    list-style: none;
    margin: 50px 50px 0px 0px;
    cursor: pointer;
}

nav{
    height: 100dvh;
    border-right: 2px solid rgba(126, 125, 125, 0.677);
}

.search{
    display: flex;
}

.project__add, .project__card{
    width: 400px;
    height: 200px;
    border: 2px solid rgba(126, 125, 125, 0.677);
    margin: 25px;
    border-radius: 20px;
    padding: 20px 20px 20px 20px;
}

.line{
    width: 88vw;
    border-bottom: 2px solid rgba(126, 125, 125, 0.512);
}

.project__card .line{
    width: 400px;

}


.project__add a{
    display: flex;
    position: relative;
    margin: 0px auto;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    height: 200px;
    cursor: pointer;
}

.header__card{
    display: flex;
   
    justify-content: space-between;
    align-items: center;
}

.more{
    cursor: pointer;
}
.card__description{
    width: 400px;
    height: 300px;
    overflow-x:hidden; 
}
.card__description h4{
    width: 400px;
    height: 300px;
}


feature-compare {
    display:flex;
    margin-top:2.5em;
}


feature-compare > * {
    position:relative;
    padding:0;
    text-align:center;
    width:25%;
}




feature-compare ul {
    list-style:none;
    margin:0;
    padding:0;
    background:#fff;
}
feature-compare li {
    white-space:nowrap;
    line-height:2.2em;
    border-top:1px solid #eee;
}
feature-compare li:first-child {
    border-top:0;
}

feature-compare h3 {
    margin:0;
    padding:.4em 0 0;
}
feature-compare .price {
    margin:0 0 .2em;
    font-size:2em;
    font-weight:bold;
}
feature-compare span {
    display:none;
}


feature-compare .button {
    display:inline-block;
    margin:.5em 0;
    padding:.4em .5em;
    background:#fff;
    color:#000;
    text-decoration:none;
    font-weight:bold;
    border-radius:.4em;
}
feature-compare > :nth-child(2) .button {
    background:#000;
    color:#fff;
}


@media (min-width:900px) {
    feature-compare {
        margin:2.5em calc(8px + 1.5625vw);
    }
    feature-compare > * {
        width:270px;
    }
    feature-compare span {
        display:inline;
    }
}

free-level,standard-level,pro-level{
   padding: 0px 10px 0px 10px;
}
standard-level{
    border-right: 2px solid rgba(126, 125, 125, 0.445);
    border-left: 2px solid rgba(126, 125, 125, 0.544);
}

article {
    border-radius: 10px;
    padding: 20px;
    color: #fff;
  }

.task__form{
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    flex-wrap: wrap;
    align-content: center;
}
.board {
    padding: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    border: 2px solid  rgba(126, 125, 125, 0.512);
    border-radius: 10px;
    position: relative;
    width: 400px;
    margin: 10px;
}
.board__id{
    display: inline-block;
    background:rgba(126, 125, 125, 0.512);
    color: #000;
    text-align: center;
    border-radius: 50%;
    padding: 0.5em  0;
    width:2em;
    height:1em;
    margin-right: 10px;
}

.item{
    border: 2px solid  rgba(126, 125, 125, 0.512);
    padding: 10px;
    margin: 10px;
    border-radius: 10px;
    width: 350px;
    height: 150px;
}
.board__dateAdd{
    margin-left: 40px;
}
.board__header{
    display: flex;
    flex-wrap: nowrap;
    flex-direction: row;
    align-items: baseline;
    justify-content: space-between;
}
.header__project{
    margin-left: 65px;
}
.board__discription{
    margin-top: 10px;
}
.board__footer{
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: space-between;
    align-items: center;
}

.board__discription{
    height: 85px;
}
.image-upload>input {
    display: none;
  }